			],
			value: '',
			radio: 3,
			radio1: 3,
			radio2: 3,
			input: '',
			total: '',
			dialogTableVisible: false,
			checkindex: '',
			listOne: []
		};
	},
	created() {
		console.log('');
		if (this.$cookies.isKey('access_token')) {
			//
			console.log('');
		} else {
			//
			this.$router.push({ name: 'Login', params: { userId: '123' } });
			console.log('');
		}
		this.categoryList();
	},
	methods: {
		categoryList() {
			this.$_loading = this.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
			});
			var data = {
				accessToken: '1565742674|145B1691263AEC04CC1722BA2EF68A86',
				id_token: this.$cookies.get('access_token'),
				posKey: '',
				wxWorkLocation: '',
				workExp: '',
				educationName: '',
				minAge: '',
				maxAge: '',
				sex: '',
				pageIndex: parseInt(this.page),
				pageSize: parseInt(10)
			};

			var _this = this;
			this.$http
				.resume(data)
				.then(res => {
					console.log(res);
					_this.$_loading.close();
					if (res.errorCode == 0) {
						var xx = res.data.page;
						for (var i = 0; i < xx.length; i++) {
							if (xx[i].sex == 0) {
								xx[i].sex = '';
							} else {
								xx[i].sex = '';
							}
							xx[i].iid = i;
						}

						this.tableData = xx;

						this.total = res.data.total;
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		onRowClick: function(row, rowIndex) {
			console.log(row);
			console.log(rowIndex);
		},
		handleCurrentChange(e) {
			console.log(e);
			this.page = e;
			this.categoryList();
		},
		toggleSelection(rows) {
			if (rows) {
				rows.forEach(row => {
					this.$refs.multipleTable.toggleRowSelection(row);
				});
			} else {
				this.$refs.multipleTable.clearSelection();
			}
		},
		goAch: function(e) {
			this.dialogTableVisible = true;
			this.checkindex = e;
			console.log(e);
		},
		handleClick(row) {
			console.log(row);
			this.dialogTableVisible = true;
			this.listOne = row;
		},
		handleSelectionChange(val) {
			console.log(val);
			this.multipleSelection = val;
		},
		one() {
			this.status = true;
		},
		one1() {
			this.status = false;
		}
	}
};
</script>

