import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
// 1. 2.vue.use 3.store 4.

const store =new Vuex.Store({
	state:{
		// 
		userinfo:{}
	},
	mutations:{
		get_user(state,data){
			state.userinfo=data;
			console.log(data);
		}
	},
	actions:{
		getUser(context,data){
			context.commit('get_user',data)
		}
	}
})

export default store;